import React, { useEffect, useState } from "react";
import { Route, Switch } from "react-router";
import { Stack, StackItem, ThemeProvider } from "@fluentui/react";
import "./App.css";
import { initializeIcons } from '@fluentui/react';
import { createTheme } from "@fluentui/style-utilities";
import Title, { IAppRouteItem } from './components/Title/Title';
import Menu from './components/Menu/Menu';
import Dock from './components/Dock/Dock';
import Home from "./pages/Home/Home";
import Feedback from "./pages/Feedback/Feedback";
import UrlEncoder from "./pages/UrlEncoder/UrlEncoder";
import Regex from "./pages/Regex/Regex";
import NotFound from "./pages/NotFound/NotFound";

const appTheme = createTheme({
    palette: {
        themePrimary: "#a8a8a8",
        themeLighterAlt: "#070707",
        themeLighter: "#1b1b1b",
        themeLight: "#323232",
        themeTertiary: "#656565",
        themeSecondary: "#949494",
        themeDarkAlt: "#b1b1b1",
        themeDark: "#bdbdbd",
        themeDarker: "#cecece",
        neutralLighterAlt: "#4a4a4a",
        neutralLighter: "#525252",
        neutralLight: "#5e5e5e",
        neutralQuaternaryAlt: "#656565",
        neutralQuaternary: "#6b6b6b",
        neutralTertiaryAlt: "#848484",
        neutralTertiary: "#c8c8c8",
        neutralSecondary: "#d0d0d0",
        neutralPrimaryAlt: "#dadada",
        neutralPrimary: "#ffffff",
        neutralDark: "#f4f4f4",
        black: "#f8f8f8",
        white: "#424242",
    },
});

initializeIcons();

interface IAppItem extends IAppRouteItem {
    excludeRoute?: boolean;
}

const App: React.FunctionComponent = () => {
    let _DismissMenu: boolean = false;
    let _AppItems: IAppItem[] = [
        {
            title: "Home",
            path: "",
            iconName: "Home",
            component: Home,
            showInDock: true,
            showInMenu: false,
            excludeRoute: true
        },
        {
            title: "Feedback",
            path: "/tools/feedback",
            iconName: "Feedback",
            component: Feedback,
            showInDock: false,
            showInMenu: true
        },
        {
            title: "URL Encoder",
            path: "/tools/url-encoder",
            iconName: "ChangeEntitlements",
            component: UrlEncoder,
            showInDock: true,
            showInMenu: true
        },
        {
            title: "Regex Tester",
            path: "/tools/regex-tester",
            iconName: "TestBeaker",
            component: Regex,
            showInDock: true,
            showInMenu: true
        }
    ];

    const [isMenuOpen, setIsMenuOpen] = useState(false);

    let _onMouseDown = () => {
        _DismissMenu = isMenuOpen;
    }

    let _onMouseUp = () => {
        window.setTimeout(() => {
            if (_DismissMenu) {
                _DismissMenu = false;
                setIsMenuOpen(false);
            }
        }, 10);
    }

    let _onMenuClicked = () => {
        setIsMenuOpen(!isMenuOpen);
    }

    let _onAppItemClicked = () => {
        if (isMenuOpen) {
            setIsMenuOpen(false);
        }
    }

    useEffect(() => {
        window.addEventListener("mousedown", _onMouseDown);
        window.addEventListener("mouseup", _onMouseUp);

        return () => {
            window.removeEventListener("mousedown", _onMouseDown);
            window.removeEventListener("mouseup", _onMouseUp);
        };
    })

    return <>
        <ThemeProvider theme={appTheme}>
            <Stack className="App" verticalFill>
                <StackItem className="AppContent" grow>
                    <Switch>
                        {_AppItems.filter(i => !i.excludeRoute).map(i => {
                            return <Route key={i.path} exact path={i.path} component={() => <Title item={i} />} />;
                        })}
                        <Route key="HomeRoute" exact path='' component={Home} />
                        <Route key="NotFoundRoute" component={NotFound} />
                    </Switch>
                </StackItem>
                <Menu isOpen={isMenuOpen} menuItems={_AppItems.filter(x => x.showInMenu)} onMenuItemClicked={_onAppItemClicked} />
                <StackItem className="AppDock" shrink>
                    <Dock activePath={window.location.pathname} dockItems={_AppItems.filter(x => x.showInDock)} onMenuClicked={_onMenuClicked} onMenuItemClicked={_onAppItemClicked} />
                </StackItem>
            </Stack>
        </ThemeProvider>
    </>;
}

export default App;