import React from 'react';
import './Dock.css';
import { DefaultButton, Stack, StackItem } from '@fluentui/react';
import { IMenuItem } from '../Menu/Menu';
import Clock from '../Clock/Clock';
import { Link } from 'react-router-dom';

interface IDockProps {
    activePath: string;
    dockItems: IMenuItem[];
    onMenuClicked: () => void;
    onMenuItemClicked: () => void;
}

const Dock: React.FunctionComponent<IDockProps> = ({ activePath, dockItems, onMenuClicked, onMenuItemClicked }) => {
    return <>
        <Stack className="Dock" horizontal tokens={{ childrenGap: 2 }}>
            <StackItem>
                <DefaultButton iconProps={{ iconName: "WindowsLogo" }} onClick={onMenuClicked} title="Menu" />
            </StackItem>

            {dockItems.length > 0 && dockItems.map(i => (<StackItem key={i.path}>
                <Link to={i.path} onClick={() => onMenuItemClicked()}>
                    <DefaultButton
                        className={(activePath === i.path) ? "active" : ""}
                        title={i.title}
                        iconProps={{ iconName: i.iconName }} />
                </Link>
            </StackItem>))}
            <StackItem align="center" >
                <Clock />
            </StackItem>
        </Stack>
    </>;
}

export default Dock;